// 
import VueImageHandler from './vue-image-handler/src/index.vue';
 
// 
const components = [VueImageHandler];
//  install  Vue  use 
const install = function(Vue) {
  // 
  if (install.installed) return;
  // 
  components.forEach(component => {
    Vue.component(component.name, component)
  });
};
// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
export default {
  //  install Vue.use() 
  install,
  // 
  VueImageHandler
};